body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  font-size: 16px;
  width: 100%;
}

.left-col {
  flex: 1;
  overflow-y: scroll;
  overflow-x: scroll;
  padding: 15px;
}

.right-col {
  flex: 1;
  overflow-y: scroll;
  padding: 15px;
}

input[type='file'],
input[type='button'],
input[type='submit'] {
  display: none;
}

.swagger-upload {
  display: inline-block;
}

.swagger-name {
  float: left;
  bottom: 10px;
  left: 10px;
  position: relative;
}

.swagger-btn {
  float: left;
}

.custom-file-action {
  border: 1px solid #ccc;
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
}
